<script>
	import {fly} from 'svelte/transition';
	import isDesktop from './../../../store/isDesktop.js';
	import adapt from './../../../script/adapt.js';
	import {onMount} from 'svelte';
	export let centerY = false;
	export let centerX = false;
	onMount(()=>{
		adapt();
	});
</script>

<div transition:fly={{y:-100,duration:200}} class="Page" style={$isDesktop?'width:'+$isDesktop+'px;margin: auto;':'width: auto;'}>
	<div class="PageContent" style="{centerY?'align-items:center;':''}{centerX?'justify-items:center;':''}">
		<slot></slot>
	</div>
</div>

<style lang="scss">
	.Page{
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		display: grid;
	}
	.PageContent{
		position: relative;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		display: grid;
		/*justify-items: center;
		align-items: center;*/
		overflow-y: auto;
		overflow-x: hidden;
	}
</style>